<template>
  <el-row :gutter="10" tag="span">
    <el-col :span="16"
      ><div class="grid-content bg-purple">
        <el-input v-model="input" placeholder="请输入内容"></el-input></div
    ></el-col>
    <el-col :span="8"
      ><div class="grid-content bg-purple">
        <el-button type="primary" @click="generateShortURL(input)"
          >生成短链接</el-button
        >
      </div></el-col
    >

    <el-col :span="8"
      ><div class="grid-content bg-purple" style="margin-top: 20px">
        <el-input v-model="shortURL"></el-input></div
    ></el-col>
    <el-col :span="8"
      ><div class="grid-content bg-purple" style="margin-top: 20px">
        <el-button
          type="primary"
          icon="el-icon-document-copy"
          v-clipboard:copy="shortURL"
          v-clipboard:success="onCopySuccess"
          v-clipboard:error="onCopyError"
          >复制短链接</el-button
        >
      </div></el-col
    >
  </el-row>
</template>

<script>
export default {
  data () {
    return {
      input: '',
      shortURL: ''
    }
  },
  methods: {
    onCopySuccess () {
      this.$message.success('复制成功')
    },
    onCopyError () {
      this.$message.error('复制失败')
    },
    generateShortURL (input) {
      this.input = input
      this.$http({
        url: '/url/gen/',
        method: 'post',
        data: JSON.stringify({
          originalURL: this.input
        }),
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .catch((error) => {
          this.$message.error(error.response.data.message)
        })
        .then((res) => {
          if (res.status === 200) {
            this.shortURL = res.data.data
          } else {
            this.$message.error(res.msg)
          }
        })
    }
  }
}
</script>



