import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import admin from '@/components/admin/admin'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      component: index
    },
    {
      path: '/admin',
      component: admin
    }
  ]
})



