<template>
  <el-row :gutter="10" tag="span">
    <el-col :span="8"
      ><div class="grid-content bg-purple">
        <el-input
          v-model="input"
          placeholder="请输入预生成短链接数量"
        ></el-input></div
    ></el-col>
    <el-col :span="8"
      ><div class="grid-content bg-purple">
        <el-button type="primary" @click="prepareShortURL(input)"
          >预生成{{ input }}条短链接</el-button
        >
      </div></el-col
    >
  </el-row>
</template>

<script>
export default {
  data () {
    return {
      input: 1000
    }
  },
  methods: {
    prepareShortURL (input) {
      this.input = input
      this.$http
        .get('/url/pre?count=' + input)
        .catch((error) => {
          this.$message.error(error.response.data.message)
        })
        .then((res) => {
          if (res.status === 200) {
            this.$message.success('操作成功！')
          }
        })
    }
  }
}
</script>



